import React, { useState } from 'react';
import Input from '../Input';
import Checkbox from '../Checkbox';
import {
  Head,
  SelectBase,
  Fabric,
  Label,
  FormGroup,
  Result,
  Cost,
  Calculation,
  RadioGroup,
  RadioLabel,
  RadioBtn,
  Button,
} from '../Shared';
import { romeOptions } from '../../config';
import OrderModal from '../OrderModal';

import { barhatColors, blackoutColors, tulleColors } from './colors';
import SelectColor from '../SelectColor';

const mapColors = {
  romeTulle: tulleColors,
  romeBlackout: blackoutColors,
  romeCloth: barhatColors,
};

const CORNICE_PRICE = 2200; //  
const TAPE_PRICE = 100; //     
const TAPE_COEF = 0.3; //    

const baseMap = romeOptions.reduce((res, i) => {
  res[i.price] = i.title;
  return res;
}, {});

const Rome = ({ option }) => {
  const [showOrderModal, toggleModal] = useState(false);
  const [color, setColor] = useState('');
  const [category, setCategory] = useState(option || null);

  const [values, setValues] = useState({
    base: option ? romeOptions.find(i => i.id === option).price : null,
    width: null,
    height: null,
    cornice: false,
    type: 'standart',
  });

  const { base, width, height, cornice, type } = values;

  const materialCost = +(base * width).toFixed(2);
  const sewingCost = +(base * width * height).toFixed(2);
  const tapeCost = +(width * (height / TAPE_COEF) * TAPE_PRICE).toFixed(2);
  const corniceCost = cornice ? +(width * CORNICE_PRICE).toFixed(2) : 0;

  console.log('>>>>>>>>>>>>', base);

  const totalPrice = +(materialCost + sewingCost + tapeCost + corniceCost).toFixed(2);

  return (
    <div>
      <RadioGroup>
        <RadioLabel>
          <RadioBtn checked={type === 'standart'}>
            <input
              type="radio"
              value="standart"
              checked={type === 'standart'}
              onChange={() => setValues({ ...values, type: 'standart' })}
            />
          </RadioBtn>
          
        </RadioLabel>
        <RadioLabel>
          <RadioBtn checked={type === 'order'}>
            <input
              type="radio"
              value="order"
              checked={type === 'order'}
              onChange={() => setValues({ ...values, type: 'order' })}
            />
          </RadioBtn>
           
        </RadioLabel>
      </RadioGroup>
      <Head>
        1.  <i></i>
      </Head>
      <SelectBase>
        {romeOptions.map((item) => (
          <Fabric
            key={item.id}
            url={item.image}
            onClick={() => {
              setColor('');
              setCategory(item.id);
              setValues({ ...values, base: item.price });
            }}
            active={category === item.id}
          >
            {item.title}
            <Cost><br />{item.price} / </Cost>
          </Fabric>
        ))}
      </SelectBase>
      {base && category ? (
        <>
          <Head>
            2.  <i></i>
          </Head>
          <SelectColor options={mapColors[category]} select={setColor} value={color} />
        </>
      ) : null}
      {base && color ? (
        <>
          <Head>
            3.  <i></i>
          </Head>
          <FormGroup>
            <Label>  ( )</Label>
            <br />
            <Input changed={(w) => setValues({ ...values, width: w })} placeholder="   " />
          </FormGroup>
          <FormGroup>
            <Label> </Label>
            <br />
            <Input changed={(h) => setValues({ ...values, height: h })} placeholder="   " />
          </FormGroup>
          <Checkbox
            onChange={(e) => setValues({ ...values, cornice: +e.target.checked })}
            checked={cornice}
            title=" "
          />
          {width && height ? (
            <Result>
              <Calculation>
                    :
                <div> : {base} * {width} = {materialCost}</div>
                <div> : {base} * {width} * {height} = {sewingCost}</div>
                <div>
                    :&nbsp;
                  {width} * ({height}/{TAPE_COEF}) * {TAPE_PRICE} = {tapeCost}
                </div>
                {cornice ? (
                  <div> : {width} * {CORNICE_PRICE} = {corniceCost}</div>
                ) : null}
              </Calculation>
              <div>
                : {totalPrice} 
                <br />
                <Button onClick={() => toggleModal(true)}> </Button>
              </div>
            </Result>
          ) : null}
          {showOrderModal ? (
            <OrderModal
              details={` , ${baseMap[base]}, ${color}, ${width}/${height}  ${cornice ? '+ ' : ''}.`}
              price={totalPrice}
              close={() => toggleModal(false)}
            />
          ) : null}
        </>
      ) : null}
    </div>
  );
};

export default Rome;
