import React, { useState } from 'react';
import Input from '../Input';
import OrderModal from '../OrderModal';

import {
  SelectBase,
  Fabric,
  Cost,
  Head,
  Label,
  FormGroup,
  Result,
  Calculation,
  Button,
} from '../Shared';

import { corniceOptions } from '../../config';

const options = corniceOptions.map(i => i.price);

const baseMap = corniceOptions.reduce((res, i) => {
  res[i.price] = i.title;
  return res;
}, {});

const PortiereTab = ({ option }) => {
  const [showOrderModal, toggleModal] = useState(false);

  const [values, setValues] = useState({
    width: null,
    corniceBase: options[option],
  });

  const setWidth = (width) => {
    setValues({ ...values, width });
  };

  const setCorniceBase = (corniceBase) => {
    setValues({ ...values, corniceBase });
  };

  const { width, corniceBase } = values;
  const corniceCost = +(width * corniceBase).toFixed(2);

  return (
    <>
      <Head>
        1.  <i></i> 
      </Head>
      <SelectBase>
        {corniceOptions.map((item) => (
          <Fabric
            key={item.id}
            url={item.image}
            onClick={() => setCorniceBase(item.price)}
            active={values.corniceBase === item.price}
          >
            {item.title}
            <Cost><br />{item.price} /</Cost>
          </Fabric>
        ))}
        <Fabric cap />
      </SelectBase>
      {corniceBase ? (
        <>
          <Head>
            2.  <i></i> 
          </Head>
          <FormGroup>
            <Label>  ( )</Label>
            <br />
            <Input changed={setWidth} placeholder="   " />
          </FormGroup>
        </>
      ) : null}
      {width ? (
        <Result>
          <Calculation>
                :
            <div> : {width} * {corniceBase} = {corniceCost}</div>
          </Calculation>
          <div>
            : {corniceCost} 
            <br />
            <Button onClick={() => toggleModal(true)}> </Button>
          </div>
        </Result>
      ) : null}

      {showOrderModal ? (
        <OrderModal
          details={` ${baseMap[corniceBase]?.toLowerCase()} ${width} .`}
          price={corniceCost}
          close={() => toggleModal(false)}
        />
      ) : null}
    </>
  );
};

export default PortiereTab;
