import React, { useState } from 'react';
import Input from '../Input';
import Checkbox from '../Checkbox';
import KsInfo from '../KsInfo';
import { portiereOptions } from '../../config';
import OrderModal from '../OrderModal';

import {
  Head,
  SelectBase,
  Fabric,
  Label,
  FormGroup,
  Result,
  Cost,
  Calculation,
  RadioGroup,
  RadioLabel,
  RadioBtn,
  Button,
} from '../Shared';

const CORNICE_REG_PRICE = 700; //   - 
const CORNICE_DEC_PRICE = 2000; //   - 
const TAPE_PRICE = 100; //     
const TAPE_COEF = 0.3; //    

const options = portiereOptions.map(i => i.price);

const baseMap = portiereOptions.reduce((res, i) => {
  res[i.price] = i.title;
  return res;
}, {});

const PortiereTab = ({ option }) => {
  const [showOrderModal, toggleModal] = useState(false);

  const [values, setValues] = useState({
    base: options[option],
    width: null,
    waves: null,
    cornice: false,
    corniceBase: CORNICE_REG_PRICE,
  });

  const setBase = (price) => {
    setValues({ ...values, base: price });
  };

  const setWidth = (width) => {
    setValues({ ...values, width });
  };

  const setWaves = (waves) => {
    setValues({ ...values, waves });
  };

  const setCornice = (event) => {
    setValues({ ...values, cornice: +event.target.checked });
  };

  const setCorniceBase = (corniceBase) => {
    setValues({ ...values, corniceBase });
  };

  const { base, width, waves, cornice, corniceBase } = values;

  const materialCost = +(base * width * waves).toFixed(2);
  const sewingCost = +(720 * width * waves).toFixed(2);
  const tapeCost = +(width * (waves / TAPE_COEF) * TAPE_PRICE).toFixed(2);
  const corniceCost = cornice ? +(width * corniceBase).toFixed(2) : 0;

  const totalPrice = +(materialCost + sewingCost + tapeCost + corniceCost).toFixed(2);

  return (
    <div>
      <Head>
        1. 
        &nbsp;
        <i></i>
      </Head>
      <SelectBase>
        {portiereOptions.map((item) => (
          <Fabric
            key={item.id}
            url={item.image}
            onClick={() => setBase(item.price)}
            active={values.base === item.price}
          >
            {item.title}
            <Cost><br />{item.price} / </Cost>
          </Fabric>
        ))}
      </SelectBase>
      {base ? (
        <>
          <Head>
            2. 
            &nbsp;
            <i></i>
          </Head>
          <FormGroup>
            <Label>  ( )</Label>
            <br />
            <Input changed={setWidth} placeholder="   " />
          </FormGroup>
          <FormGroup>
            <Label>
                ( )
              <KsInfo />
            </Label>
            <br />
            <Input changed={setWaves} placeholder="  -  2.5" />
          </FormGroup>
          <Checkbox onChange={setCornice} checked={cornice} title=" " />
          {cornice ? (
            <RadioGroup>
              <RadioLabel>
                <RadioBtn checked={corniceBase === CORNICE_REG_PRICE}>
                  <input
                    type="radio"
                    value="option1"
                    checked={corniceBase === CORNICE_REG_PRICE}
                    onChange={() => setCorniceBase(CORNICE_REG_PRICE)}
                  />
                </RadioBtn>
                 ({CORNICE_REG_PRICE} /)
              </RadioLabel>
              <RadioLabel>
                <RadioBtn checked={corniceBase === CORNICE_DEC_PRICE}>
                  <input
                    type="radio"
                    value="option2"
                    checked={corniceBase === CORNICE_DEC_PRICE}
                    onChange={() => setCorniceBase(CORNICE_DEC_PRICE)}
                  />
                </RadioBtn>
                 ({CORNICE_DEC_PRICE} /)
              </RadioLabel>
            </RadioGroup>
          ) : null}

          {width && waves ? (
            <Result>
              <Calculation>
                    :
                <div> : {base} * {width} * {waves} = {materialCost}</div>
                <div> : 720 * {width} * {waves} = {sewingCost}</div>
                <div>
                    :&nbsp;
                  {width} * ({waves}/{TAPE_COEF}) * {TAPE_PRICE} = {tapeCost}
                </div>
                {cornice ? (
                  <div> : {width} * {corniceBase} = {corniceCost}</div>
                ) : null}
              </Calculation>
              <div>
                : {totalPrice} 
                <br />
                <Button onClick={() => toggleModal(true)}> </Button>
              </div>
            </Result>
          ) : null}

          {showOrderModal ? (
            <OrderModal
              details={`, ${baseMap[base]},  ${width} ,  ${waves} ${cornice ? `+ ${corniceBase === CORNICE_REG_PRICE ? '' : ''} ` : ''}.`}
              price={totalPrice}
              close={() => toggleModal(false)}
            />
          ) : null}
        </>
      ) : null}
    </div>
  );
};

export default PortiereTab;
